
import { Options, Vue } from 'vue-class-component';

const IconSymbolMap: Record<string, string> = {
    'search': '\uf002',
    'th-list'   : '\uf00b',
    'check' : '\uf00c',
    'times' : '\uf00d',
    'cog'   : '\uf013',
    'clock' : '\uf017',
    'redo'  : '\uf01e',
    'edit'  : '\uf044',
    'info-circle'   : '\uf05a',
    'arrow-left': '\uf060',
    'arrow-up'  : '\uf062',
    'plus'  : '\uf067',
    'calendar-alt'  : '\uf073',
    'folder-open'   : '\uf07c',
    'globe' : '\uf0ac',
    'angle-up'  : '\uf106',
    'angle-down': '\uf107',
    'ellipsis-v': '\uf142',
    'trash' : '\uf1f8',
    'sync-alt'  : '\uf2f1',
    'sign-in-alt'   : '\uf2f6',
    'redo-alt'  : '\uf2f9',
    'map-marker-alt': '\uf3c5',
    'mobile-alt': '\uf3cd',
    'tachometer-alt': '\uf3fd',
    'brain' : '\uf5dc',
}

@Options({
    props: {
        icon: String
    },
})
export default class MaterialIconRaw extends Vue {

    private static nameRegex = /^[a-z\-]+$/;

    icon!: string;

    get symbol(): string {
        if (MaterialIconRaw.nameRegex.test(this.icon)) {
            return IconSymbolMap[this.icon];
        } else {
            return this.icon;
        }
    }
}
